.aboutBody {
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin-bottom: 10vh;
}

.aboutBox {
    background-color: #213A5C;
    min-height: 850px;
    min-width: 90vh;
    max-width: 90vh;
    text-align: right;
}

.aboutText {
    margin: 10%;
    color: #8caac8;
    max-width: 30vw;
}

.skillsText {
    margin-top: 5%;
    margin-left: 10%;
    margin-right: 10%;
    color: #213A5C;
}

.pfp {
    display: flex;
    justify-content: center;
    margin-top: 50px;
}

.skillsBox {
    background-color: #6F8FAF;
    min-height: 850px;
    text-align: left;
}

.aboutBody img {
    display: block;
    width: 300px;
}

.icons {
    height: 70px;
    width: 100px;
}

.icons1 {
    height: 25px;
    width: 35px;
    color:white;    
}

.stack {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 35vh;
}

.info {
    display: flex;
    justify-content: right;
    align-items: center;
    height: 10vh;
}

.stats {
    margin-top: 50px;
    flex-direction: row;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.stats {
    color: white;
    font-size: 20px;
}

.stats h3{
    color:#213A5C;
}